@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&family=Stick&display=swap);
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App__options {
  display: flex;
  width: 100%;
  min-height: 100vh;
  justify-content: space-evenly;
  align-items: center;
  font-family: Roboto, sans-serif;
  flex-direction: column;
}

.App__options a {
  color: white;
  font-size: 2rem;
  text-decoration: none;
  padding: 1rem;
  display: block;
}

.App__option {
  width: 40%;
  background: rgba(200, 200, 200, 0.2);
  text-align: center;
  border: 1px solid black;
}

.App__option:hover {
  background: rgba(200, 200, 200, 0.6);
  border: 1px solid white;
}

.Timer {
  display: flex;
  flex-direction: column;
  background: rgba(150, 150, 150, 0.2);
  padding: 0 2rem;
}

.Timer__block {
  padding: 1rem 0;
  display: flex;
  justify-content: space-between;
}

.Timer__label {
  font-family: "Roboto", sans-serif;
  font-size: 2rem;
}

.Timer__value {
  font-family: "Stick", sans-serif;
  color: yellow;
  font-size: 2rem;
  width: 6rem;
  text-align: right;
}

.Timer__summary {
  font-size: 1rem;
}

.Toronto {
  text-align: center;
  min-height: 100vh;
}

.Toronto::after {
  content: "";
  background: url(/how-long/static/media/cn_tower.bfbe9c53.jpg) no-repeat;
  background-size: cover;
  background-position: center center;
  -webkit-filter: brightness(20%);
          filter: brightness(20%);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

.Toronto__header {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Churrita {
  text-align: center;
  min-height: 100vh;
}

.Churrita::after {
  content: "";
  background: url(/how-long/static/media/churrita.cd02454f.jpg) no-repeat;
  background-size: contain;
  background-position: center center;
  -webkit-filter: brightness(20%);
          filter: brightness(20%);
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

.Churrita__header {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

